.buzzer {
  border-radius: 100%;
  width: 70vw;
  height: 70vw;
  max-width: 400px;
  max-height: 400px;
  border: 5px solid lightgrey;
}
.pressable {
  background-color:rgb(255, 0, 0);
}
.nonPressable {
  background-color: rgb(167, 26, 26);
}
.pressable:hover {
  background-color: rgb(240, 26, 26);
}
.pressable:active {
	position:relative;
	top:1px;
  background-color: rgb(167, 26, 26);
}